<template>
    <div id="msg">
        <div class="msg_content">
            <Message v-bind:key="item.id" v-for="item in messages" v-bind:data="item"/>
        </div>
    </div>
</template>

<script>
    import Message from '@/components/Messages_Item'

    export default {
        props: {
            messages: Array
        },
        components: {
            Message
        },
        mounted: () => {
            let msg = document.getElementById('msg');
            msg.scrollTo(0, msg.scrollHeight);
        }
    }
</script>

<style lang="scss">
    ::-webkit-scrollbar:vertical {
        display: none;
    }
    
    #msg {
        width: 70%;
        min-width: 220px;
        background:aliceblue;
        overflow-y: scroll;
        position: relative;

        .msg_content {
            margin: 10px;
            display: flex;
            flex-direction: column;
            
            > div {
                margin-bottom: 10px;
                
                &:last-child {
                    margin-bottom: 0;
            }
        }
        }
    }
</style>